@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);

body {
  background-color: #121212;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  padding: 15px;
  text-align: center;
}
.active {
  background-color: #383838;
}

.heading {
  font-size: 3rem;
  margin: 1.5rem 1rem;
}
.about p {
  margin: 1.5rem 1rem;
}
.about ul {
  list-style: none;
  margin: 1.5rem 1rem;
}
.about ul li {
  color: #84dfe2;
  font-weight: 700;
  font-size: 1.3rem;
}
.about ul li i {
  margin-right: 2px;
}
.about .description {
  font-size: 1.4rem;
  margin: 1rem 0;
}
.button {
  padding: 1rem;
  margin-top: 20px;
  margin-right: 20px;
  color: black;
  background-color: #84dfe2;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  letter-spacing: 1px;
}
.button:hover {
  cursor: pointer;
}
button:focus {
  outline: none;
}
.bg-color {
  background-color: #1d1d1d;
}

.experience p {
  margin: 0 1rem;
}
.experience ul {
  margin-left: 3rem;
  list-style: none;
}
.experience ul li {
  margin: 15px;
}
h5 {
  font-size: 2rem;
  font-weight: 400;
}
.home {
  height: 80vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 5rem;
}
.home .heading {
  font-size: 4rem;
  font-weight: 500;
}
.home .sub-heading {
  font-size: 2rem;
  font-weight: 500;
}
img {
  width: 130px;
  height: 130px;
  object-fit: cover;
  -webkit-animation: pulse linear 1.5s infinite;
          animation: pulse linear 1.5s infinite;

  margin-bottom: 10px;
}
.interest {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.main {
  margin-left: 20vw;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  overflow: hidden;
}
.poppins {
  font-family: "Poppins", sans-serif;
}
.project-cards {
  display: -webkit-flex;
  display: flex;
}
.section {
  height: 100vh;
  margin-left: 1rem;
  margin-top: 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 5rem;
}
.sidebar-container {
  width: 20vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}
.sidebar-profile {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0.5rem;
  border-bottom: 0.5px solid white;
  text-align: center;
}
.sidebar-links ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 1.5rem;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  color: white;
  margin-bottom: 1rem;
  width: 100%;
}
.sidebar-links ul li {
  width: 100%;
}
.sidebar-links ul li a {
  display: block;
  width: 100%;
}
.sidebar-links ul li button {
  width: 100%;
  margin: 0.2rem 0;
  padding: 0.5rem;
  background-color: transparent;
  border: 0;
  font-size: 1.5rem;
}
.sidebar-links ul li:hover {
  background-color: #383838;
}

.sidebar {
  z-index: 10;
  height: 100vh;
}

.skills {
  display: -webkit-flex;
  display: flex;
}
.skills ul {
  margin-right: 2rem;
}
@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(132, 223, 226, 0.8);
  }

  50% {
    box-shadow: 0 0 0 15pxrgba (132, 223, 226, 0.6);
  }
  50% {
    box-shadow: 0 0 0 20px rgba(132, 223, 226, 0.4);
  }

  100% {
    box-shadow: 0 0 0 22px rgba(132, 223, 226, 0);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(132, 223, 226, 0.8);
  }

  50% {
    box-shadow: 0 0 0 15pxrgba (132, 223, 226, 0.6);
  }
  50% {
    box-shadow: 0 0 0 20px rgba(132, 223, 226, 0.4);
  }

  100% {
    box-shadow: 0 0 0 22px rgba(132, 223, 226, 0);
  }
}

@media screen and (max-width: 1224px) {
  .about {
    margin-left: 0;
    padding: 5px;
  }
  .about p {
    text-align: center;
  }
  .button-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 14px;
  }

  .experience ul {
    list-style: none;
    margin: 15px;
  }
  .heading {
    font-size: 3rem;

    text-align: center;
  }
  .home .heading {
    text-align: center;
    font-size: 2.6rem;
    font-weight: 500;
  }
  .home .sub-heading {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
  }
  .main {
    margin-left: 1rem;
  }
  .project-cards {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .sidebar {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 200ms ease-out;
    transition: transform 200ms ease-out;
    transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
  }
  .sidebar-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: -webkit-transform 200ms ease-in;
    transition: transform 200ms ease-in;
    transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
  }
  .sidebar-container {
    width: 70vw;
    position: fixed;
    top: 0;
    z-index: 10;
  }
  .sidebar-profile {
    padding: 0.1rem;
  }

  .skills {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
}

.card{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;cursor:pointer;width:22em;max-width:80%;padding:2em 0;background:#1d1d1d;box-shadow:0 0 6px 0 rgba(32,32,36,0.12);transition:all 0.35s ease;color:white;margin:1rem}.card::before,.card::after{content:"";position:absolute;top:0;left:0;width:100%;background:white;height:4px}.card::before{width:0;opacity:0;transition:opacity 0 ease, width 0 ease;transition-delay:0.5s}.card::after{width:100%;background:white;transition:width 0.5s ease}.card .content{width:18em;max-width:80%}.card .logo{margin:0 0 1em;width:10.625em;transition:all 0.35s ease}.card h6{color:white;font-weight:600;text-transform:uppercase;margin:0;letter-spacing:2px}.card .hover_content{overflow:hidden;max-height:0;-webkit-transform:translateY(1em);transform:translateY(1em);transition:all 0.55s ease}.card .hover_content p{margin:1.5em 0 0;color:white;line-height:1.4em}.card:hover{width:24em;box-shadow:0 10px 20px 0 rgba(32,32,36,0.12)}.card:hover::before{width:100%;opacity:1;transition:opacity 0.5s ease, width 0.5s ease;transition-delay:0}.card:hover::after{width:0;opacity:0;transition:width 0 ease}.card:hover .logo{margin-bottom:0.5em}.card:hover .hover_content{max-height:10em;-webkit-transform:none;transform:none}

